.QuoteCard {
    display: flex;
    padding: 1.25em 1.25em 1.25em  0;
    width: 800px;
    border: 1px solid #f8f8f8;
    border-radius: 2px;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    font-size: 1.2em;
}

.QuoteCard img {
    display: flex;
    max-height: 220px;
    margin-right: 1.5rem;
}

.QuoteCard blockquote {
    margin: 0 0 1rem 0;
}